import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { CourseService } from '../../angular-services/course.service';
import { CourseCategoryService } from '../../angular-services/course-category.service';
import { CourseCategory } from '../../classes/course-category';

@Component({
  selector: 'app-new-course-form-admin',
  templateUrl: './new-course-form.component.html',
  styleUrls: ['./new-course-form.component.css']
})
export class NewCourseFormComponent implements OnInit {

  formNewCourse: FormGroup;
  alert: any = { type: '', message: '', show: false };
  categories: CourseCategory[] = [];
  mediaType = 'courses';
  urlObjects: string = 'https://viruscursinho.com.br:3002/courses';
  showModalSelectFile: boolean = false;

  constructor(private courseService: CourseService, private categoryService: CourseCategoryService) { }

  ngOnInit() {
    this.initForm();
    this.getCategories();
  }

  initForm() {
    this.formNewCourse = new FormGroup({
      title: new FormControl('', Validators.required),
      description: new FormControl('', Validators.required),
      photo: new FormControl('', Validators.required),
      idCategory: new FormControl('', Validators.required),
      status: new FormControl('', Validators.required),
    });
  }

  save() {
    if (this.formNewCourse.valid) {
      let data = this.formNewCourse.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      this.courseService.addCourse(data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao salvar os dados!';
            this.alert.show = true;
          }
        }
      )
    }
  }

  getCategories() {
    this.categoryService.getCategories().subscribe(
      response => {
        if (response !== undefined) {
          for (let category of response) {
            if (category.status) {
              this.categories.push(category);
            }
          }
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao salvar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  changeStatusModalSelectFile() {
    if (this.showModalSelectFile) {
      this.showModalSelectFile = false;
    } else {
      this.showModalSelectFile = true;
    }
  }

  setUrlObject(link) {
    this.formNewCourse.patchValue({
      photo: link
    });
    this.showModalSelectFile = false;
  }

}
